               <h3>Términos y Condiciones IRIS TRAVEL GROUP</h3>
               <p>Los términos y condiciones que se presentan a continuación, regulan las relaciones con nuestros usuarios y establecen la forma en que se deben utilizar nuestros productos y servicios, le recomendamos leerlos con detenimiento, previo a cualquier solicitud. Una vez expresada su conformidad con dichos términos, significa que usted leyó y comprendió todos los términos y condiciones relativos a las políticas formuladas. 
               </p>

               <h3>Pagos</h3>
               <p>Los pagos se realizarán por adelantado, por transferencia, efectivo o por tarjeta de crédito la que será debitada, dentro de un plazo de 24 horas, después de acordado con el Comercial que le brinda asistencia. La Agencia solo acepta pagos en línea con las tarjetas de crédito siguientes: Visa, MasterCard y American Express.
                  Los precios de los servicios pueden estar sujetos a cambios, con excepción de aquellos casos en que la reservación ha sido confirmada. En caso de cambios relativos a un aumento en el precio/tarifa, la suma se abonará en el momento del cambio y no será efectivo hasta tanto no se haya recibido dicho pago y hasta que el suministrador no haya confirmado la disponibilidad. 
               </p>

               <h3>Cancelaciones - Reembolsos</h3>
               <p>Todos los servicios no son reembolsables, si el cliente tuviese necesidad de cambiar o cancelar sus planes de viaje, será responsabilidad suya notificar a la Agencia por escrito de dicho cambio.</p>
               <p>Una vez efectuado el pago de la reservación, se aplicará la siguiente política de cancelación, penalidades y No Show:</p>
