               <h3>Alojamiento en Hoteles o Casas de Renta</h3>
               <p>Debe informarse al cliente que es imprescindible presentar el <strong>pasaporte</strong> o <strong>carné de identidad</strong> en el momento del check in. Algunos hoteles exigen también la presentación de la tarjeta de embarque/desembarque.</p>
               <p>Las casas se pueden rentar por habitaciones o la casa completa según sean las condiciones de cada casa en particular.</p>
               <p><strong>Reservaciones y Pagos:</strong> Le ofrecemos la disponibilidad de habitaciones y la confirmación del servicio en un término de 24 a 48 horas, para toda una gama de hoteles en diferentes destinos. En casos de afectaciones de última hora en el Hotel/Casa confirmado/a, La Agencia garantizará su remplazo de similar categoría, en la misma ciudad. El cargo total incluye los cargos por concepto de habitación e impuestos, al igual que las tarifas de acceso y reservación. Cargos incidentales, tales como parqueo, llamadas telefónicas y servicio de habitación, serán cubiertos por el cliente, directamente con el hotel.</p>
