               <h3>Vuelos Internacionales</h3>
               <ul class="list-disc pl-10">
                  <li>Las autoridades correspondientes tendrán el derecho de negar la transportación a personas y/o su equipaje, cuando de conformidad con las normas aplicables determine que ello podría implicar un riesgo o peligro, o bien, que la transportación de dichas personas, equipajes y/o pertenencias se encuentre prohibida por disposiciones o restricciones legales aplicables. Con este fin, podrán revisar a las personas, sus equipajes y/o pertenencias que se llevarán a bordo de la aeronave.</li>
                  <li>El pasajero es responsable de traer consigo al momento de la documentación del vuelo toda la documentación personal correspondiente completa y vigente y de cumplir con las disposiciones sobre aduanas, migración, sanidad, de policía y otras disposiciones que sean aplicables para permitirles la salida del país, así como el ingreso al o los países de destino, así como en los lugares en que, en su caso, se llegare a realizar escalas.</li>
                  <li>En caso de comprar solo una ruta (OneWay), el pasajero deberá llevar consigo su boleto de retorno el día del vuelo, para presentarlo en el momento del Check In en el aeropuerto, en caso de que se le solicite</li>
                  <li>La reservación será válida solamente para la fecha que fue adquirida; por lo tanto, el pasajero se obliga a cubrir un cargo adicional si cambia de fecha, vuelo, o si la cancela. El que las modificaciones mencionadas sean permitidas, depende de la anticipación con que se soliciten.</li>
                  <li>Las solicitudes de cambio de fecha solo podrán realizarse con un mínimo de 15 días previos a la fecha del boleto confirmado. De confirmarse el cambio de fecha, procederá un cargo extra, que será comunicado al cliente en la fecha de solicitud del cambio.</li>
                  <li>En caso de No Show, aplicará una penalidad del 100%. Los reembolsos se harán sólo a la persona que adquirió el boleto, pasaje o emitidos a la orden, la cual proporcionará los documentos de pago original.</li>
                  <li>Algunas tarifas y promociones no aplican en puentes y/o días festivos.</li>
                  <li>Si el pasajero no utiliza su boleto o no realizó ningún cambio antes de la salida del vuelo, éste no será válido en otra fecha.</li>
                  <li>El pasajero deberá presentarse en el mostrador asignado para mostrar los documentos de su vuelo cuatro horas antes de la salida.</li>
                  <li>El check in se cierra una hora y 30 minutos antes de la salida del vuelo.</li>
                  <li>Los artículos de valor, permanecerán bajo custodia del pasajero a bordo del avión. No somos responsables del traslado, daño o pérdida de los mismos.</li>
                  <li>No somos responsables por daño o pérdida de artículos de valor, tales como monedas, joyas, piedras preciosas, objetos de arte, documentos, acciones y certificados negociables, aparatos electrónicos, artículos de fotografía, video o por artículos de fácil descomposición; los cuales no se consideran como equipaje y son transportados sin conocimiento ni consentimiento de la Empresa. Todo reclamo referente a equipajes deberá formularse antes de abandonar el aeropuerto.</li>
                  <li>La documentación de artículos frágiles se realiza bajo riesgo del pasajero.</li>
                  <li>No se permite la transportación de ningún tipo de mascota, ni cajas de huevo.</li>
                  <li>En caso de transportar artículos como armas de fuego, equipos de pesca o de competencia, etc., tendrá que efectuar un procedimiento especial de documentación, por lo cual debe presentarse 3 horas antes de salida del vuelo, con los certificados correspondientes para su traslado y la identificación oficial del titular, para el llenado correspondiente y la autorización del departamento de la PFP en el aeropuerto.</li>
               </ul>

               <h3>Renta de Autos en Destino Cuba</h3>
               <p>Para la renta de autos, Iris Travel Group pone a su disposición un importante grupo de coches económicos y medios, con transmisión automática y manual. Las compañías solo confirman categorías y no modelos específicos de coches.</p>
               <p>Las oficinas de renta operan solamente de 9 am a 6 pm excepto las situadas en los aeropuertos, y en La Habana en las oficinas del Vedado cita en Calle 3ra y Paseo, y en la oficina de Playa cita en Hotel Neptuno.</p>
               <p>El punto de renta en que se confirmará su renta está sujeto a disponibilidad del transportista. En caso se diferir con el que usted reservó se le notificará oportunamente, de no estar usted de acuerdo puede cancelar su reserva sin gastos siempre que lo haga antes de pasadas 48 horas de haber recibido la información.</p>
               <p><strong>Para recoger el auto: </strong>Al recoger el coche Usted debe presentarles a los funcionarios de la rentadora:</p>
               <ol class="list-decimal pl-10">
                  <li>El voucher de confirmación de la agencia</li>
                  <li>Pasaporte.</li>
                  <li>Licencia de conducción válida para todos los conductores (las licencias de conducción deben ser válidas en todo el período de alquiler y tener más de un año de antigüedad)</li>
                  <li>
                     <p>Tarjeta de crédito para pago del seguro y depósito de garantía</p>
                  </li>
               </ol>
                  
               <p>En caso de no poseer cualquiera de estos documentos, no se ejecutará el servicio y se considerará un No Show, aplicando la penalidad correspondiente.</p>
               <p>Los pagos podrán realizarse con tarjetas en Moneda Libremente Convertible (MLC) y con tarjeta de pre-pago que puede ser adquirida en el Aeropuerto en Cuba, también pueden utilizarse tarjetas VISA o Mastercard, siempre que estas no sean de bancos estadounidenses o alguna de sus sucursales.</p>
               <p>
                  <strong>Política de cancelación:</strong>
                  <ul class="list-disc pl-10">
                     <li>Si cancela la reserva al menos 7 días antes de la fecha de su llegada señalada, se le reembolsará la totalidad de la suma que pagó por la reservación.</li>
                     <li>Si cancela dentro de un plazo de entre 7 días y 72 horas antes de la fecha de su llegada, recibirá el monto de la reservación menos el precio de dos días de arrendamiento.</li>
                     <li>Si cancela en menos de 72 horas antes de la fecha le serán descontados tres días de la reserva.</li>
                     <li>Si liquida el auto antes de la fecha indicada al inicio de la renta no se le devolverá el importe correspondiente a los días no utilizados.</li>
                  </ul>
               </p>
               <p>Los reembolsos serán cargados a su tarjeta de crédito dentro de un plazo de 72 horas, después de haber recibido la cancelación.</p>

               <p><strong>Combustible: </strong>El coche es entregado con el depósito lleno de combustible y el cliente deberá pagar por adelantado el valor de dicho combustible. Si en el momento de recogida del coche el depósito de gasolina no está lleno, usted recibirá instrucciones del personal de la agencia de renta sobre cómo actuar. Los costos de combustibles deben ser pagados con tarjeta de crédito, usando siempre gasolina especial o diesel según corresponda. El cliente deberá devolver el coche con el depósito lo más vacío posible, puesto que la rentadora no reembolsará ningún dinero por el combustible sobrante.</p>
               <p><strong>Conductor adicional: </strong>Los conductores adicionales serán incluidos en el contrato en el momento de entrega del auto, con un cargo adicional a pagar directo en la rentadora, especificando el número de su licencia de conducción (vigente) y el del pasaporte. El seguro (Daños por Colisión del Vehículo) no cubrirá los perjuicios provocados por el conductor, de no cumplirse estos requisitos, aun en el caso que tuviese una licencia de conducción vigente.</p>
               <p><strong>Equipamiento adicional: </strong>Ofrecemos asientos para niños. Para garantizar la disponibilidad de un asiento para niños, usted deberá solicitarlo en el momento de la reservación.</p>
               <p><strong>Tarifa de retorno: </strong>Las compañías arrendadoras aplicarán cargos extras por concepto de devolución del vehículo en un lugar diferente al de donde se inició la renta. La tarifa dependerá de la distancia entre las ciudades.</p>
               <p>
                  <strong>Edad Mínima y Máxima: </strong>
                  El arrendador exige que el cliente cumpla con los requisitos de edad mínima para poder alquilar un vehículo:
                  <ul class="list-disc pl-10">
                        <li>21 años: Skoda Fabia, Audi A3, A4, A6, VW Sharan y otros (y un mínimo de 3 años de experiencia).</li>
                        <li>30 años: Audi A4 cabriolet & Audi TT.</li>
                        <li>La edad máxima establecida para rentar un auto es menor de 70 años.</li>
                  </ul>
               </p>

               <p><strong>Seguro: </strong>Existen dos modalidades de ventas: Con y Sin seguro.</p>

               <p>El seguro CDW (contra daños debidos a la colisión del auto), reduce la responsabilidad del cliente en caso de accidente, siempre que no haya violado ningunas de las cláusulas del contrato, suscrito en el momento de alquilar el auto</p>

               <p><strong>Licencia de Conducción: </strong>El cliente le presentará al arrendador, una licencia de conducción vigente, emitida al menos 6 meses antes del momento de suscribir el contrato. En caso de que la licencia de conducción, emitida en el país de origen del cliente, no estuviese redactada en inglés o español, el cliente deberá presentar una licencia de conducción internacional.</p>
               <p><strong>Tarifa de Aeropuerto: </strong>Todas las reservas confirmadas en los aeropuertos internacionales tendrán un suplemento de 20.00 USD, lo cual se pagará en el momento de la apertura del contrato de arrendamiento del auto.</p>
               <p>
                  <strong>Devolución del vehículo: </strong>
                  El cliente devolverá el auto en la fecha, hora, y la agencia acordada. En caso de un cambio, el cliente lo notificará al arrendador, y solicitará su aprobación antes de efectuar el cambio.<br/>
                  Además, el auto será devuelto en las mismas condiciones en que fue arrendado. De no ser así, el arrendador cargará al cliente por concepto de auto-riesgo, según lo acordado en el contrato de arrendamiento.
               </p>

               <h3>RECLAMACIONES</h3>
               <p>Las reclamaciones se efectuarán a través del Comercial o del Representante de La Agencia. Las quejas que fundamenten una devolución o indemnización, deben realizarse por escrito antes de las 72 horas después de ocurrido el hecho, y antes de abandonar el cliente el país. En el caso de robos, extravíos de propiedades y otros daños personales similares, se requiere la denuncia a las autoridades policiales por parte del cliente. La Agencia y los proveedores, no se hacen responsables por equipajes y efectos personales olvidados en las unidades de transporte y/o Hoteles.</p>
